h1, h2, h3, h4, h5, h6, a, p, input, ul, li {
  margin: 0;
  padding: 0;
}

body{
  display: flex;
  align-items: center;
  background-color: #000;
  color: white;
  font-family: "Inter";
  flex-direction: column;
}

.page{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0;
  max-width: 100vw;
}